import store from '@/store/store'
// 引入基本模板
let echarts = require('echarts/lib/echarts')
// 引入柱状图,折线图,饼图组件
require('echarts/lib/chart/bar')
require('echarts/lib/chart/line')
require('echarts/lib/chart/pie')
// 引入提示框,title,legend,toolbox组件
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')
require('echarts/lib/component/legend')
require('echarts/lib/component/toolbox')

export default {
  data () {
    return {
      // 新增标签data系列
      dynamicTags: ['baidu'],
      inputVisible: false,
      inputValue: '',
      colors: ['#386FFF', '#50B97E', '#FF8D57', '#F34B62', '#37A4F9', '#BA11E0', '#874DF9'],
      taskReportData: [
        {
          tagId: '100',
          tagName: '性别',
          subTag: ['男', '女'],
          subTagValue: [
            {
              taskid: 'XXX100',
              tagItem: [1200, 800]
            },
            {
              taskid: 'XXX200',
              tagItem: [1000, 600]
            }
          ]
        },
        {
          tagId: '200',
          tagName: '年龄',
          subTag: ['20-25岁', '26-30岁'],
          subTagValue: [
            {
              taskid: 'XXX100',
              tagItem: [800, 700]
            }
          ]
        }
      ]
    }
  },

  created () {
    this.getComonTitle()
  },

  mounted () {
    // this.drawLine1()

    // 遍历的e就是数组的每项数据
    this.taskReportData.forEach(e => {
      // console.log(e)
      this.drawLine1(e)
    })
  },

  methods: {
    // 新增对比系列方法
    handleClose (tag) {
      this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1)
    },
    showInput () {
      this.inputVisible = true
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    handleInputConfirm () {
      let inputValue = this.inputValue
      if (inputValue) {
        this.dynamicTags.push(inputValue)
      }
      this.inputVisible = false
      this.inputValue = ''
    },
    // 切换样式
    toggleStyle1 (item) {
      this.drawLine1(item)
    },
    toggleStyle2 (item) {
      this.drawLine2(item)
      // console.log(item)
    },
    toggleStyle3 (item) {
      this.drawLine3(item)
    },
    // 版式一转换taskReportData数据里面的 子项数据格式
    transform1 (e) {
      let arr = e.subTagValue
      let newArr = []
      arr.forEach((e, i) => {
        newArr.push(
          {
            name: e.taskid,
            type: 'bar',
            data: e.tagItem,
            itemStyle: {
              normal: {
                color: this.colors[i % this.colors.length]
              }
            }
          }
        )
      })
      // console.log(newArr)
      return newArr
    },
    // 版式二转换taskReportData数据里面的 子项数据格式
    transform2 (e) {
      let arr = e.subTagValue
      let newArr = []
      arr.forEach((e, i) => {
        newArr.push(
          {
            name: e.taskid,
            type: 'line',
            data: e.tagItem,
            itemStyle: {
              normal: {
                color: this.colors[i]
              }
            }
          }
        )
      })
      return newArr
    },
    // 版式三转换taskReportData数据里面的 子项数据格式
    transform3 (e) {
      let newArr = []
      let label = e.subTag
      let arr = e.subTagValue
      arr.forEach((e, i) => {
        let arrxx = []
        e.tagItem.forEach((e, i) => {
          arrxx.push(
            {
              value: e,
              name: label[i],
              itemStyle: {
                normal: {
                  color: this.colors[i]
                }
              }
            }
          )
        })
        let circleW = (30 + (i * 24)) + '%'
        let circleH = (40 + (i * 22)) + '%'
        newArr.push(
          {
            name: e.taskid,
            type: 'pie',
            radius: [circleW, circleH],
            label: {
              normal: { position: 'inner',
                formatter: '{b}\n{d}%'
              }
            },
            data: arrxx
          }
        )
      })
      return newArr
    },

    // echarts画图方法
    drawLine1 (e) {
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById(e.tagId))
      let newArr = this.transform1(e)
      // 绘制图表
      myChart.setOption({
        tooltip: {
          trigger: 'axis'
        },
        xAxis: [
          {
            type: 'category',
            data: e.subTag
          }
        ],
        yAxis: [
          {
            type: 'value'
          }
        ],
        series: newArr
      }, true)
    },
    drawLine2 (e) {
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById(e.tagId))
      let newArr = this.transform2(e)
      // 绘制图表
      myChart.setOption({
        tooltip: {
          trigger: 'axis'
        },
        xAxis: [
          {
            type: 'category',
            data: e.subTag
          }
        ],
        yAxis: [
          {
            type: 'value'
          }
        ],
        series: newArr
      }, true)
    },
    drawLine3 (e) {
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById(e.tagId))
      // 绘制图表
      myChart.setOption({
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}:{c}({d}%)'
        },
        series: this.transform3(e)
      }, true)
    },
    getComonTitle () {
      if (this.comonTitle === '/report') {
        this.$store.commit('editComonTitle', {info: '查看报告'})
      }
    }
  },

  store,

  computed: {
    comonTitle () {
      return this.$store.state.comonTitle
    }
  }
}



