import Vue from 'vue'

import Vuex from 'vuex'

Vue.use(Vuex)

// <!-- 数据存储状态 -->

const state = {

  usersession: 0,
  comonTitle: '新标签申请',
  routerData: ''

}

// <!-- 同步操作state的方法 -->

const mutations = {

  edit (state, payload) {
    state.usersession = payload.num
  },
  editComonTitle (state, payload) {
    state.comonTitle = payload.info
  },
  editRouterData (state, payload) {
    state.routerData = payload.info
  }
}

// <!-- 设置导出项 -->

export default new Vuex.Store({

  state,

  mutations

})



