import store from '@/store/store'
export default {
  computed: {
    comonTitle () {
      return this.$store.state.comonTitle
    },
    routerData () {
      return this.$store.state.routerData
    }
  },

  data () {
    return {
    }
  },

  store,

  methods: {
    // 叉叉点击返回
    jump () {
      switch (this.routerData) {
        case '/newapply':
          this.$router.push('/bg_search')
          break
        case '/allapply':
          this.$router.push('/bg_search')
          break
        case '/applydetail':
          this.$router.push('/allapply')
          break
        case '/style1':
          this.$router.push('/bg_personal')
          break
        case '/style2':
          this.$router.push('/bg_personal')
          break
        case '/style3':
          this.$router.push('/bg_personal')
          break
        case '/label':
          this.$router.push('/bg_group')
          break
        case '/id':
          this.$router.push('/bg_group')
          break
        case '/report':
          this.$router.push('/bg_group')
          break
        case '/taskdetail':
          this.$router.push('/taskmanage')
          break
        case '/statedetail':
          this.$router.push('/applymanage')
          break
        case '/statedetailReject':
          this.$router.push('/applymanage')
          break
        case '/statedetailExam':
          this.$router.push('/applymanage')
          break
        case '/statereject':
          this.$router.push('/applymanage')
          break
        case '/contentedit':
          this.$router.push('/contentmanage')
          break
        case '/information':
          this.$router.go(-1)
          break
        case '/newsdetail':
          this.$router.go(-1)
          break
        default:
      }
    }
  }
}



