import store from '@/store/store'

// 引入基本模板
let echarts = require('echarts/lib/echarts')
// 引入柱状图组件
// require('echarts/lib/chart/bar')
require('echarts-wordcloud')
// 引入提示框和title组件
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')

export default {
  name: 'hello',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },

  created () {
    this.getComonTitle()
  },

  mounted () {
    this.drawLine()
  },

  methods: {
    drawLine () {
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById('myChart'))
      // 绘制图表
      myChart.setOption({
        series: [{
          type: 'wordCloud',

          // 要绘制的“云”的形状: 可以是任何一个极坐标方程(回调函数)或已存在的关键字
          // 可用关键字: circle (默认) cardioid (apple or heart shape curve, the most known polar equation), diamond (
          // alias of square), triangle-forward, triangle, (alias of triangle-upright, pentagon, and star.
          shape: 'triangle',

          // 将从绘图文本中排除白色区域的轮廓图像。
          // 形状选项将继续作为云的形状来应用。
          // maskImage: maskImage,

          // 左/上/左/宽/高/右/下用于定位文字云, 默认放在中心，大小为75％x 80％。
          // left: 'center',
          // top: 'center',
          // width: '50%',
          // height: '80%',
          // right: null,
          // bottom: null,

          // 数据中的值将映射到的文本大小范围。
          // 默认为最小12px，最大60px。
          sizeRange: [12, 60],

          // 文本旋转范围和步长。
          rotationRange: [-45, 45],
          rotationStep: 15,

          // 用于标记画布可用性的网格大小（以像素为单位）
          // 网格大小越大，单词之间的差距越大。
          gridSize: 10,

          // 设置为true以允许在画布之外部分绘制单词。
          // 允许大于要绘制的画布大小的单词
          drawOutOfBound: false,

          // 全局文本样式
          textStyle: {
            normal: {
              fontFamily: 'sans-serif',
              fontWeight: 'bold',
              // Color can be a callback function or a color string
              color: function () {
                // Random color
                return 'rgb(' + [
                  Math.round(Math.random() * 160),
                  Math.round(Math.random() * 160),
                  Math.round(Math.random() * 160)
                ].join(',') + ')'
              }
            },
            emphasis: {
              shadowBlur: 10,
              shadowColor: '#333'
            }
          },

          // 数据是一个数组。数组每个值都必须具有name和value属性。
          data: [{
            name: '读书交友',
            value: 10000,
            textStyle: {
              normal: {
                color: 'black'
              },
              emphasis: {
                color: 'red'
              }
            }
          }, {
            name: '房产',
            value: 618
          }, {
            name: '读书交友',
            value: 438
          }, {
            name: '专业技术人员',
            value: 405
          }, {
            name: '百度',
            value: 246
          }, {
            name: '影视音乐',
            value: 224
          }, {
            name: '8000及以上',
            value: 189
          }, {
            name: '18~24',
            value: 148
          }, {
            name: '旅游出行',
            value: 111
          }, {
            name: '百度',
            value: 965
          }, {
            name: '资讯',
            value: 847
          }, {
            name: '专业技术人员',
            value: 582
          }, {
            name: '影视音乐',
            value: 555
          }, {
            name: '房产',
            value: 550
          }, {
            name: '同城交友',
            value: 462
          }, {
            name: '搜索影视音乐',
            value: 366
          }, {
            name: '音乐',
            value: 360
          }, {
            name: '地图',
            value: 282
          }, {
            name: '百度',
            value: 273
          }, {
            name: '房产',
            value: 265
          }, {
            name: '读书交友',
            value: 438
          }, {
            name: '专业技术人员',
            value: 405
          }, {
            name: '百度',
            value: 246
          }, {
            name: '影视音乐',
            value: 224
          }, {
            name: '8000及以上',
            value: 189
          }, {
            name: '18~24',
            value: 148
          }, {
            name: '旅游出行',
            value: 111
          }, {
            name: '百度',
            value: 965
          }, {
            name: '资讯',
            value: 847
          }, {
            name: '专业技术人员',
            value: 582
          }, {
            name: '影视音乐',
            value: 555
          }, {
            name: '房产',
            value: 550
          }, {
            name: '同城交友',
            value: 462
          }, {
            name: '搜索影视音乐',
            value: 366
          }, {
            name: '音乐',
            value: 360
          }, {
            name: '地图',
            value: 282
          }, {
            name: '百度',
            value: 273
          }, {
            name: '房产',
            value: 265
          }]
        }]
      })
    },
    getComonTitle () {
      if (this.comonTitle === '/style1') {
        this.$store.commit('editComonTitle', {info: '查看详情'})
      }
    }
  },

  store,

  computed: {
    comonTitle () {
      return this.$store.state.comonTitle
    }
  }
}



