import store from '@/store/store'

export default {
  created () {
    this.getComonTitle()
  },

  methods: {
    getComonTitle () {
      if (this.comonTitle === '/style3') {
        this.$store.commit('editComonTitle', {info: '查看详情'})
      }
    }
  },

  store,

  computed: {
    comonTitle () {
      return this.$store.state.comonTitle
    }
  }
}



