import store from '@/store/store'
export default {
  data () {
    return {
      applyName: '车险部——凤凰男',
      form: {
        reason: ''
      }
    }
  },

  created () {
    this.getComonTitle()
  },

  methods: {
    besure () {
      this.$router.push('/applymanage')
    },
    getComonTitle () {
      if (this.comonTitle === '/statedetail') {
        this.$store.commit('editComonTitle', {info: '查看详情'})
      }
    }
  },

  store,

  computed: {
    comonTitle () {
      return this.$store.state.comonTitle
    }
  }
}



