import store from '@/store/store'
export default {
  data () {
    return {
      userId: '0',
      userType: '',
      options: [],
      sidebar: ''
    }
  },

  created () {
    // 设置侧边栏高亮
    this.setSidebar()

    this.userId = localStorage.getItem('userId')
    if (this.userId === '1') {
      this.userType = '普通用户'
      this.options = [
        { index: '/bg_search', title: '标签查询', icon: 'iconfont icon-tag', id: 1 },
        { index: '/bg_personal', title: '个体画像', icon: 'iconfont icon-geren', id: 2 },
        { index: '/bg_group', title: '群体画像', icon: 'iconfont icon-qunzu', id: 3 }
      ]
    } else if (this.userId === '2') {
      this.userType = '管理员'
      this.options = [
        { index: '/bg_search', title: '标签查询', icon: 'iconfont icon-tag', id: 1 },
        { index: '/bg_personal', title: '个体画像', icon: 'iconfont icon-geren', id: 2 },
        { index: '/bg_group', title: '群体画像', icon: 'iconfont icon-qunzu', id: 3 },
        { index: '/labelmanage', title: '标签管理', icon: 'iconfont icon-tag', id: 4 },
        { index: '/taskmanage', title: '任务管理', icon: 'iconfont icon-geren', id: 5 },
        { index: '/applymanage', title: '申请管理', icon: 'iconfont icon-qunzu', id: 6 },
        { index: '/authoritymanage', title: '权限管理', icon: 'iconfont icon-tag', id: 7 },
        { index: '/contentmanage', title: '内容管理', icon: 'iconfont icon-geren', id: 8 }
      ]
    }
  },

  methods: {
    toHomepage () {
      this.$router.push('/main_login')
    },
    logout () {
      localStorage.setItem('userId', '0')
      this.$router.push('/main_login')
    },
    setSidebar () {
      this.sidebar = this.routerData
    }
  },

  store,

  computed: {
    routerData () {
      return this.$store.state.routerData
    }
  }
}



