import store from '@/store/store'
export default {
  data () {
    return {
      title: '党领导全面依法治国 习近平强调这十六个字',
      brief: '2019年1月15日至16日，中央政法工作会议在北京召开。中共中央总书记、国家主席、中央军委主席习近平出席会议并发表重要讲话。',
      content: '<!--导语-->' +
      '<p class="one-p"><img src="//inews.gtimg.com/newsapp_bt/0/7729924187/1000" class="content-picture">' +
      ' </p>' +
      '<p class="one-p">2月16日，《求是》杂志发表习近平总书记重要文章——《加强党对全面依法治国的领导》。习近平在文章中提出，必须坚持实现党<strong>领导立法、保证执法、支持司法、带头守法，</strong>健全党领导全面依法治国的制度和工作机制，通过法定程序使党的主张成为国家意志、形成法律，通过法律保障党的政策有效实施，确保全面依法治国正确方向。</p>' +
      '<p class="one-p">党的十八大以来，习近平曾多次围绕党“领导立法、保证执法、支持司法、带头守法”四个方面发表重要论述，立意高远、切中肯綮。央视网特进行梳理，以飨读者。</p>' +
      '<p class="one-p"><!--H2-->立法体现党的主张和人民意愿<!--/H2--></p>' +
      '<p class="one-p">党的政策是国家法律的先导和指引，是立法的依据和执法司法的重要指导。</p>' +
      '<p class="one-p">——2014年1月7日，在中央政法工作会议上的讲话</p>' +
      '<p class="one-p">党和法治的关系是法治建设的核心问题。全面推进依法治国这件大事能不能办好，最关键的是方向是不是正确、政治保证是不是坚强有力，具体讲就是要坚持党的领导，坚持中国特色社会主义制度，贯彻中国特色社会主义法治理论。</p>' +
      '<p class="one-p">——2014年10月20日，关于《中共中央关于全面推进依法治国若干重大问题的决定》的说明</p>' +
      '<p class="one-p">法是党的主张和人民意愿的统一体现，党领导人民制定宪法法律，党领导人民实施宪法法律，党自身必须在宪法法律范围内活动，这就是党的领导力量的体现。</p>' +
      '<p class="one-p">——2015年2月2日，在省部级主要领导干部学习贯彻十八届四中全会精神全面推进依法治国专题研讨班开班式上的讲话</p>' +
      '<p class="one-p">要紧紧抓住全面依法治国的关键环节，完善立法体制，提高立法质量。</p>' +
      '<p class="one-p">——2019年2月16日，《求是》杂志发表重要文章《加强党对全面依法治国的领导》</p>' +
      '<p class="one-p"><!--H2-->执法要给予支持和保护<!--/H2--></p>' +
      '<p class="one-p">执法者必须忠于法律。</p>' +
      '<p class="one-p">——2013年2月23日，在十八届中央政治局第四次集体学习时的讲话</p>' +
      '<p class="one-p">对执法机关严格执法，只要符合法律和程序的，各级党委和政府都要给予支持和保护，不要认为执法机关给自己找了麻烦，也不要担心会给自己的形象和政绩带来什么不利影响。</p>' +
      '<p class="one-p">——2014年1月7日，《严格执法，公正司法》《十八大以来重要文献选编》（上），中央文献出版社2014年版，第723页</p>' +
      '<p class="one-p">要推进严格执法，理顺执法体制，完善行政执法程序，全面落实行政执法责任制。</p>' +
      '<p class="one-p">——2019年2月16日，《求是》杂志发表重要文章《加强党对全面依法治国的领导》</p>' +
      '<p class="one-p">党的十八大以来，我们制定和修订了140多部中央党内法规，出台了一批标志性、关键性、基础性的法规制度，有规可依的问题基本得到解决，下一步的重点是执规必严，使党内法规真正落地。</p>' +
      '<p class="one-p">——2019年2月16日，《求是》杂志发表重要文章《加强党对全面依法治国的领导》</p>' +
      '<p class="one-p"><!--H2-->司法促进社会公平正义<!--/H2--></p>' +
      '<p class="one-p">党的领导是社会主义法治的根本保证，坚持党的领导是我国社会主义司法制度的根本特征和政治优势。</p>' +
      '<p class="one-p">——2015年3月24日，在十八届中共中央政治局第二十一次集体学习时的讲话</p>' +
      '<p class="one-p">深化司法体制改革，是要更好坚持党的领导、更好发挥我国司法制度的特色、更好促进社会公平正义。</p>' +
      '<p class="one-p">——2014年1月7日，在中央政法工作会议上的讲话</p>' +
      '<p class="one-p">要全面落实司法责任制，让司法人员集中精力尽好责、办好案，提高司法质量、效率、公信力。</p>' +
      '<p class="one-p">——2019年1月15日至16日，在中央政法工作会议上的讲话</p>' +
      '<p class="one-p">要支持司法机关依法独立行使职权，健全司法权力分工负责、相互配合、相互制约的制度安排。</p>' +
      '<p class="one-p">——2019年2月16日，《求是》杂志发表重要文章《加强党对全面依法治国的领导》</p>' +
      '<p class="one-p"><!--H2-->守法带头尊崇法治、敬畏法律<!--/H2--></p>' +
      '<p class="one-p">党领导人民制定宪法和法律，党领导人民执行宪法和法律，党自身必须在宪法和法律范围内活动，真正做到党领导立法、保证执法、带头守法。</p>' +
      '<p class="one-p">——2012年12月4日，在首都各界纪念现行宪法公布施行30周年大会上的讲话</p>' +
      '<p class="one-p">要发挥依法治国和依规治党的互补性作用，确保党既依据宪法法律治国理政，又依据党内法规管党治党、从严治党。</p>' +
      '<p class="one-p">——2019年2月16日，《求是》杂志发表重要文章《加强党对全面依法治国的领导》</p>' +
      '<p class="one-p">领导干部必须带头尊崇法治、敬畏法律，了解法律、掌握法律，遵纪守法、捍卫法治，厉行法治、依法办事，不断提高运用法治思维和法治方式深化改革、推动发展、化解矛盾、维护稳定的能力，做尊法学法守法用法的模范，以实际行动带动全社会尊法学法守法用法。</p>' +
      '<p class="one-p">——2019年2月16日，《求是》杂志发表重要文章《加强党对全面依法治国的领导》</p>' +
      '<p class="one-p">各级党组织和全体党员要带头尊法学法守法用法，任何组织和个人都不得有超越宪法法律的特权，绝不允许以言代法、以权压法、逐利违法、徇私枉法。</p>' +
      '<p class="one-p">——2017年10月18日，在中国共产党第十九次全国代表大会上的报告</p>'
    }
  },

  created () {
    this.getComonTitle()
  },

  methods: {
    getComonTitle () {
      if (this.comonTitle === '/newsdetail') {
        this.$store.commit('editComonTitle', {info: '新闻详情'})
      }
    }
  },

  store,

  computed: {
    comonTitle () {
      return this.$store.state.comonTitle
    }
  }
}



