import store from '@/store/store'
export default {
  data () {
    return {
      form: {
        name: '凤凰男1',
        desc: '籍贯农村，在城市就业的男性20-35岁之间',
        reason: '针对以上类型男性，需要精准营销',
        style: '20-35岁+籍贯农村+大城市工作',
        user: 'elandy131',
        email: 'jack@163.com',
        Department: '交付部'
      },
      imageUrl: '',
      active: 1
    }
  },

  created () {
    this.getComonTitle()
  },

  methods: {
    skip1 () {
      this.$router.push('/allapply')
    },
    skip2 () {
      this.$router.push('/search')
    },
    getComonTitle () {
      if (this.comonTitle === '/applydetail') {
        this.$store.commit('editComonTitle', {info: '查看详情'})
      }
    }
  },

  store,

  computed: {
    comonTitle () {
      return this.$store.state.comonTitle
    }
  }
}



