export default {
  data () {
    return {
      input: '',
      items: [{id: '00001', name: '基本属性'}, {id: '00002', name: '保险属性'},
        {id: '00005', name: '客户属性'}],
      activeName: '',
      items1: [{id: '01001', name: '个人状况'}, {id: '01002', name: '家庭状况'},
        {id: '01005', name: '资产状况'}, {id: '01003', name: '财务状况'},
        {id: '01006', name: '性格特征'}, {id: '01004', name: '活动区域'},
        {id: '01007', name: '信用特征'}],
      activeName1: '',
      items2: [{id: '02001', name: '性别'}, {id: '02002', name: '年龄'},
        {id: '02005', name: '星座'}, {id: '02003', name: '生肖'},
        {id: '02006', name: '工作单位性质'}, {id: '02004', name: '职业'},
        {id: '02007', name: '执业资格'}, {id: '02008', name: '职称'}, {id: '02009', name: '职位'},
        {id: '02010', name: '工作年限'}, {id: '02011', name: '员工标志'}, {id: '02012', name: '学历'},
        {id: '02013', name: '政治面貌'}, {id: '02014', name: '本地居住年数'}, {id: '020', name: '现居住地所属城市等级'},
        {id: '02016', name: '户口性质'}, {id: '02017', name: '健康状况'}],
      activeName2: '',
      items3: [{id: '03001', name: '男'}, {id: '03002', name: '女'}],
      activeName3: '',
      items4: [],
      activeName4: '',
      class_showOne: true,
      class_showTwo: true,
      class_showThree: true,
      class_showFour: true
    }
  },
  methods: {
    newApply () {
      this.$router.push('/newapply')
      console.log(1)
    },
    allApply () {
      this.$router.push('/allapply')
      console.log(1)
    },
    selectItem (item) {
      this.activeName = item.id
      this.activeName1 = ''
      this.class_showOne = true
      this.class_showTwo = false
      this.class_showThree = false
      this.class_showFour = false
    },
    selectItem1 (item) {
      this.activeName1 = item.id
      this.activeName2 = ''
      this.class_showTwo = true
      this.class_showThree = false
      this.class_showFour = false
    },
    selectItem2 (item) {
      this.activeName2 = item.id
      this.activeName3 = ''
      this.class_showThree = true
      this.class_showFour = false
    },
    selectItem3 (item) {
      this.activeName3 = item.id
      this.activeName4 = ''
      this.class_showFour = true
    },
    selectItem4 (item) {
      this.activeName4 = item.id
    }

  }
}



