import store from '@/store/store'
export default {
  data () {
    return {
      applyName: '车险部——凤凰男',
      form: {
        labelName: '凤凰男',
        labelExplain: '籍贯农村，就业在大城市的20-35岁的男性。',
        proMode: '籍贯三线城市+工作地大城市+男性+20-35岁。',
        labelReason: '我部针对凤凰男新推出农村父母养老基金，需要实现精准营销。',
        labelPerson: 'elandy131',
        assessingOfficer: '管理员',
        org: '车险部二处',
        email: 'jack@163.com'
      }
    }
  },

  created () {
    this.getComonTitle()
  },

  methods: {
    besure () {
      this.$router.go(-1)
    },
    stateReject () {
      this.$router.push('/statereject')
    },
    getComonTitle () {
      if (this.comonTitle === '/statedetail') {
        this.$store.commit('editComonTitle', {info: '查看详情'})
      }
    }
  },

  store,

  computed: {
    comonTitle () {
      return this.$store.state.comonTitle
    }
  }
}



