import store from '@/store/store'
export default {
  data () {
    return {
      applyName: '车险部——凤凰男',
      input: '',
      form: {
        id: '21545645',
        title: '车险部新闻',
        brief: '新闻概要，1+1',
        detailUrl: 'https://www.baidu.com'
      }
    }
  },

  created () {
    this.getComonTitle()
  },

  methods: {
    besure () {
      this.$router.go(-1)
    },
    stateReject () {
      this.$router.push('/statereject')
    },
    getComonTitle () {
      if (this.comonTitle === '/contentedit') {
        this.$store.commit('editComonTitle', {info: '编辑详情'})
      }
    }
  },

  store,

  computed: {
    comonTitle () {
      return this.$store.state.comonTitle
    }
  }
}



