import store from '@/store/store'
export default {
  data () {
    return {
      textarea: ''
    }
  },

  created () {
    this.getComonTitle()
  },

  methods: {
    getComonTitle () {
      if (this.comonTitle === '/id') {
        this.$store.commit('editComonTitle', {info: '新建任务'})
      }
    }
  },

  store,

  computed: {
    comonTitle () {
      return this.$store.state.comonTitle
    }
  }
}



