export default {
  data () {
    return {
      editorOption: {
        modules: {
          toolbar: [
            ['bold', 'italic', 'underline', 'strike'],
            ['blockquote'],
            [{ 'list': 'ordered' }, { 'list': 'bullet' }],
            [{ 'script': 'sub' }, { 'script': 'super' }],
            [{ 'indent': '-1' }, { 'indent': '+1' }],
            [{ 'size': ['small', false, 'large', 'huge'] }],
            [{ 'color': [] }, { 'background': [] }],
            ['clean'],
            [{ 'align': [] }, 'image']
          ]
        },
        placeholder: '请输入文章内容 ...'
      },
      articleTitle: '',
      articleContent: '',
      news_title_input: '',
      news_brief_input: '',
      active: 0,
      activeName: 'one',
      messages: 'Vue CLI aims to be the standard tooling baseline for the Vue ecosystem. It ensures the various build tools work smoothly together with sensible defaults so you can focus on writing your app instead of spending days wrangling with configurations. At the same time, it still offers the flexibility to tweak the config of each tool without the need for ejecting.',
      checked1: false,
      checked2: true,
      checked3: false,
      value: 30,
      imgRadio: '1',
      messageRadio: '1',
      otherRadio: '1',
      imgPreview: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1550231157863&di=ac80cdf25390050b4330942f579d2c97&imgtype=0&src=http%3A%2F%2Fwww.cbdio.com%2Fimage%2Fattachement%2Fjpg%2Fsite2%2F20160405%2F3417eb9bbd90186dce0c0e.jpg',
      tableData: [{
        id: '21545645',
        title: '车险部新闻',
        brief: '新闻概要，1+1',
        detailUrl: 'https://www.baidu.com',
        date: '2019-02-02 12:12:20'
      }, {
        id: '21545645',
        title: '车险部新闻',
        brief: '新闻概要，1+1',
        detailUrl: 'https://www.baidu.com',
        date: '2019-02-02 12:12:20'
      }, {
        id: '21545645',
        title: '车险部新闻',
        brief: '新闻概要，1+1',
        detailUrl: 'https://www.baidu.com',
        date: '2019-02-02 12:12:20'
      }],
      formLabelWidth: '120px',
      textarea: ''
    }
  },
  methods: {
    formatTooltip (val) {
      return val + 'M'
    },
    mes1 () {
      this.messages = 'Vue CLI aims to be the standard tooling '
    },
    mes2 () {
      this.messages = 'baseline for the Vue ecosystem. It ensures the various '
    },
    mes3 () {
      this.messages = '这是新的模板'
    },
    next () {
      if (this.active++ > 1) this.active = 0
    },
    handleDelete (index, row) {
      console.log(index, row)
      this.$confirm('此操作将永久删除, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    handleClose (done) {
      this.$confirm('确认关闭？')
        .then(_ => {
          done()
        })
        .catch(_ => {})
    },
    toContentEdit () {
      this.$router.push('/contentedit')
    }
  }
}



