import store from '@/store/store'
export default {
  data () {
    return {
      form: {
        name: '',
        channel: '',
        institution: '',
        salesman: ''
      }
    }
  },

  created () {
    this.getComonTitle()
  },

  methods: {
    label () {
      this.$router.push('/label')
    },
    id () {
      this.$router.push('/id')
    },
    onSubmit () {
      console.log('submit!')
    },
    getComonTitle () {
      if (this.comonTitle === '/label') {
        this.$store.commit('editComonTitle', {info: '新建任务'})
      }
    }
  },

  store,

  computed: {
    comonTitle () {
      return this.$store.state.comonTitle
    }
  }
}



