import store from '@/store/store'
export default {
  data () {
    return {
      taskName: '车险部双十一分析',
      formData: {
        taskAccount: '车险部双十一活动',
        chooseType: '标签圈人',
        chooseElement: '渠道一、渠道二',
        labelLogic: '车险部',
        labelChoice: '张三',
        labels: '年龄20-35岁与年收入8000以上或大城市工作'
      }
    }
  },

  created () {
    this.getComonTitle()
  },

  methods: {
    besure () {
      this.$router.go(-1)
    },
    getComonTitle () {
      if (this.comonTitle === '/taskdetail') {
        this.$store.commit('editComonTitle', {info: '任务详情'})
      }
    }
  },

  store,

  computed: {
    comonTitle () {
      return this.$store.state.comonTitle
    }
  }
}



