import store from '@/store/store'

// 引入基本模板
let echarts = require('echarts/lib/echarts')
// 引入柱状图组件
require('echarts/lib/chart/tree')
// 引入提示框和title组件
require('echarts/lib/component/tooltip')
// require('echarts/lib/component/title')
export default {
  created () {
    this.getComonTitle()
  },

  mounted () {
    this.drawLine()
  },

  methods: {
    drawLine () {
      let myChart = echarts.init(document.getElementById('main'), 'macarons')

      let option = {
        // 提示框组件
        tooltip: {
          // 触发类型，默认：item
          trigger: 'item',
          // 提示框触发的条件，默认mousemove|click（鼠标点击和移动时触发）。可选mousemove：鼠标移动时，click：鼠标点击时，none：
          triggerOn: 'mousemove'
        },
        // 系列列表
        series: [
          {
            type: 'tree',
            // 数据,可以用ajax获取
            data: [
              {
                'children': [
                  {
                    'children': [
                      {
                        'children': [
                          {
                            'children': [
                              {
                                'children': [],
                                'name': '低压车间表计171'
                              },
                              {
                                'children': [],
                                'name': '低压车间表计172'
                              }
                            ],
                            'name': '低压车间表计71'
                          },
                          {
                            'children': [],
                            'name': '低压车间表计72'
                          },
                          {
                            'children': [],
                            'name': '低压车间表计73'
                          }
                        ],
                        'name': '低压关口表计01A-1-1'
                      },
                      {
                        'children': [
                          {
                            'children': [],
                            'name': '低压车间表计81'
                          },
                          {
                            'children': [],
                            'name': '低压车间表计82'
                          }
                        ],
                        'name': '低压关口表计01A-1-2'
                      }
                    ],
                    'name': '高压子表计01A-1'
                  },
                  {
                    'children': [
                      {
                        'children': [],
                        'name': '低压关口表计101'
                      },
                      {
                        'children': [],
                        'name': '低压关口表计102'
                      },
                      {
                        'children': [],
                        'name': '低压关口表计103'
                      }
                    ],
                    'name': '高压子表计01A-2'
                  },
                  {
                    'children': [
                      {
                        'children': [],
                        'name': '低压关口表计101'
                      },
                      {
                        'children': [],
                        'name': '低压关口表计101'
                      },
                      {
                        'children': [],
                        'name': '低压关口表计101'
                      }
                    ],
                    'name': '高压子表计01A-3'
                  }
                ],
                'name': '高压总表计01A'
              }
            ],

            // 距离上的距离
            top: '10%',
            left: '20%',
            bottom: '20%',
            right: '20%',

            // layout: 'radial',
            // 排列方式，纵向
            orient: 'TB',
            // 标记类型包括 'circle'圆, 'rect'正方形, 'roundRect'圆角正方形, 'triangle'三角,
            //             'diamond'菱形, 'pin'水滴, 'arrow'箭头, 'none'空, 'image://url'图片
            symbol: 'diamond',
            // 标记的大小，就是那个小圆圈，默认7
            symbolSize: 40,
            // 树图初始展开的层级(深度),根节点是第0层,如果设置为 -1 或者 null 或者 undefined，所有节点都将展开。
            initialTreeDepth: 3,

            // 线的样式
            lineStyle: {
              color: '#000',
              width: 1,
              type: 'solid',
              // 树图边的曲度,数字越小越直,默认0.5
              curveness: 0.6
            },

            // 每个节点所对应的标签的样式
            label: {
              normal: {
                position: 'inside', // 标签的位置
                verticalAlign: 'middle', // 文字垂直对齐方式，默认自动。可选：top，middle，bottom
                align: 'center', // 文字水平对齐方式，默认自动。可选：top，center，bottom
                fontSize: 10 // 标签文字大小
              }
            },

            // 叶子节点的特殊配置，如上面的树图示例中，叶子节点和非叶子节点的标签位置不同
            leaves: {
              label: {
                normal: {
                  position: 'inside',
                  verticalAlign: 'middle',
                  align: 'center'
                }
              }
            },

            expandAndCollapse: true, // 子树折叠和展开的交互，默认打开
            animationDuration: 550, // 初始动画的时长，支持回调函数,默认1000
            animationDurationUpdate: 750 // 数据更新动画的时长，默认300
          }
        ]
      }

      myChart.setOption(option)
    },
    getComonTitle () {
      if (this.comonTitle === '/style2') {
        this.$store.commit('editComonTitle', {info: '查看详情'})
      }
    }
  },

  store,

  computed: {
    comonTitle () {
      return this.$store.state.comonTitle
    }
  }
}



