import store from '@/store/store'
export default {
  data () {
    return {
      form: {
        name: '',
        desc: '',
        style: '',
        reason: '',
        user: 'elandy131',
        Department: '交付部',
        email: 'elandy131@163.com'
      },
      imageUrl: '',
      active: 0
    }
  },

  created () {
    this.getComonTitle()
  },

  methods: {
    next () {
      this.active = 1
    },
    skip1 () {
      this.$router.push('/allapply')
    },
    skip2 () {
      this.$router.push('/bg_search')
    },
    getComonTitle () {
      if (this.comonTitle === '/newapply') {
        this.$store.commit('editComonTitle', {info: '申请新标签'})
      }
    }
  },

  store,

  computed: {
    comonTitle () {
      return this.$store.state.comonTitle
    }
  }
}



