export default {
  data () {
    return {
      dialogLoginVisible: false,
      dialogRegisterVisible: false,
      form: {
        name: '',
        password: ''
      },
      formLabelWidth: '80px',
      userId: '0',
      userType: '',
      options: []
    }
  },

  created () {
    this.userId = localStorage.getItem('userId')
    if (!this.userId) {
      this.userId = '0'
    }
    if (this.userId === '1') {
      this.userType = '普通用户'
      this.options = [
        { text: '标签查询', value: '/bg_search', id: 1 },
        { text: '个体画像', value: '/bg_personal', id: 2 },
        { text: '群体画像', value: '/bg_group', id: 3 }
      ]
    } else if (this.userId === '2') {
      this.userType = '管理员'
      this.options = [
        { text: '标签查询', value: '/bg_search', id: 1 },
        { text: '个体画像', value: '/bg_personal', id: 2 },
        { text: '群体画像', value: '/bg_group', id: 3 },
        { text: '标签管理', value: '/labelmanage', id: 4 },
        { text: '任务管理', value: '/taskmanage', id: 5 },
        { text: '申请管理', value: '/applymanage', id: 6 },
        { text: '权限管理', value: '/authoritymanage', id: 7 },
        { text: '内容管理', value: '/contentmanage', id: 8 }
      ]
    }
  },

  methods: {
    logout () {
      localStorage.setItem('userId', '0')
      // this.$router.go(0)
      location.reload()
    },
    toLogin () {
      if (this.form.name === 'user' && this.form.password === 'user') {
        localStorage.setItem('userId', '1')
        this.userId = localStorage.getItem('userId')
        this.userType = '普通用户'
        this.options = [
          { text: '标签查询', value: '/bg_search' },
          { text: '个体画像', value: '/bg_personal' },
          { text: '群体画像', value: '/bg_group' }
        ]
        this.dialogLoginVisible = false
      } else if (this.form.name === 'admin' && this.form.password === 'admin') {
        localStorage.setItem('userId', '2')
        this.userId = localStorage.getItem('userId')
        this.userType = '管理员'
        this.options = [
          { text: '标签查询', value: '/bg_search' },
          { text: '个体画像', value: '/bg_personal' },
          { text: '群体画像', value: '/bg_group' },
          { text: '标签管理', value: '/labelmanage' },
          { text: '任务管理', value: '/taskmanage' },
          { text: '申请管理', value: '/applymanage' },
          { text: '权限管理', value: '/authoritymanage' },
          { text: '内容管理', value: '/contentmanage' }
        ]
        this.dialogLoginVisible = false
      }
    }
  }
}



