import store from '@/store/store'
export default {
  data () {
    return {
      input: '',
      tableData: [{
        id: '00001',
        applyName: '凤凰男1',
        subTime: '2016-05-02',
        approveTime: '2016-07-02',
        name: '审批中',
        email: 'elandy@163.com'
      }, {
        id: '00002',
        applyName: '凤凰男2',
        subTime: '2016-05-04',
        approveTime: '2016-07-04',
        name: '审批中',
        email: 'elandy@163.com'
      }, {
        id: '00003',
        applyName: '凤凰男3',
        subTime: '2016-05-01',
        approveTime: '2016-07-01',
        name: '审批中',
        email: 'elandy@163.com'
      }, {
        id: '00004',
        applyName: '凤凰男4',
        subTime: '2016-05-03',
        approveTime: '2016-07-03',
        name: '审批中',
        email: 'elandy@163.com'
      }],
      currentPage1: 5,
      currentPage2: 5,
      currentPage3: 5,
      currentPage4: 4
    }
  },

  created () {
    this.getComonTitle()
  },

  methods: {
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
    },
    newApply () {
      this.$router.push('/newapply')
    },
    details () {
      this.$router.push('applydetail')
    },
    getComonTitle () {
      if (this.comonTitle === '/allapply') {
        this.$store.commit('editComonTitle', {info: '申请记录'})
      }
    }
  },

  store,

  computed: {
    comonTitle () {
      return this.$store.state.comonTitle
    }
  }
}



