import Vue from 'vue'
import Router from 'vue-router'

// 首页
import HomePageLogin from '@/views/homepage_login/HomePageLogin.vue'
import MainLogin from '@/views/homepage_login/main_login/MainLogin.vue'

// 后台页面
import Background from '@/views/background/Background.vue'
// 404页面
import NotFound from '@/views/background/notfound/NotFound.vue'

import BgSearch from '@/views/background/bg_search/BgSearch.vue'
import BgPersonal from '@/views/background/bg_personal/BgPersonal.vue'
import BgGroup from '@/views/background/bg_group/BgGroup.vue'

import labelManage from '@/views/background/labelmanage/labelManage.vue'
import TaskManage from '@/views/background/taskmanage/TaskManage.vue'
import ApplyManage from '@/views/background/applymanage/ApplyManage.vue'
import AuthorityManage from '@/views/background/authoritymanage/AuthorityManage.vue'
import ContentManage from '@/views/background/contentmanage/ContentManage.vue'
import AuthorityBlank from '@/views/background/authoritymanage/authorityblank/AuthorityBlank.vue'
import AccessConfiguration from '@/views/background/authoritymanage/accessconfiguration/AccessConfiguration.vue'

// common公用部分
import Common from '@/views/common/Common.vue'
// 新闻详情
import NewsDetail from '@/views/homepage_login/newsdetail/NewsDetail.vue'
// 个人信息
import Information from '@/views/homepage_login/information/Information.vue'

// 标签查询子页面
import NewApply from '@/views/background/bg_search/newapply/NewApply.vue'
import AllApply from '@/views/background/bg_search/allapply/AllApply.vue'
import ApplyDetail from '@/views/background/bg_search/applydetail/ApplyDetail.vue'
// 个体画像子页面
import Individual from '@/views/background/bg_personal/individual/Individual.vue'
import Style1 from '@/views/background/bg_personal/individual/style1/Style1.vue'
import Style2 from '@/views/background/bg_personal/individual/style2/Style2.vue'
import Style3 from '@/views/background/bg_personal/individual/style3/Style3.vue'
// 群体画像子页面
import NewTask from '@/views/background/bg_group/newtask/NewTask.vue'
import Label from '@/views/background/bg_group/newtask/label/Label.vue'
import Id from '@/views/background/bg_group/newtask/id/Id.vue'
import Report from '@/views/background/bg_group/report/Report.vue'

// 任务管理子页面
import TaskDetail from '@/views/background/taskmanage/taskdetail/TaskDetail.vue'
import TaskDetailId from '@/views/background/taskmanage/taskdetail_id/TaskDetailId.vue'
// 申请管理子页面
import StateDetail from '@/views/background/applymanage/statedetail/StateDetail.vue'
import StateReject from '@/views/background/applymanage/statereject/StateReject.vue'
import StateDetailExam from '@/views/background/applymanage/statedetailExam/StateDetail.vue'
import StateDetailReject from '@/views/background/applymanage/statedetailReject/StateDetail.vue'
// 内容管理子页面
import ContentEdit from '@/views/background/contentmanage/contentedit/ContentEdit.vue'

Vue.use(Router)

const router = new Router({
  routes: [
    { path: '/', redirect: '/homepage_login' },
    {
      path: '/homepage_login',
      component: HomePageLogin,
      redirect: '/main_login',
      children: [
        { path: '/main_login', component: MainLogin }
      ]
    },
    {
      path: '/common',
      component: Common,
      children: [
        // 首页新闻详情
        { path: '/newsdetail', component: NewsDetail },
        // 个人信息页面
        { path: '/information', component: Information },
        // 前台部分
        // 标签查询
        { path: '/newapply', component: NewApply },
        { path: '/allapply', component: AllApply },
        { path: '/applydetail', component: ApplyDetail },
        // 个体画像
        {
          path: '/individual',
          component: Individual,
          redirect: '/style1',
          children: [
            { path: '/style1', component: Style1 },
            { path: '/style2', component: Style2 },
            { path: '/style3', component: Style3 }
          ]
        },
        // 群体画像
        {
          path: '/newtask',
          component: NewTask,
          redirect: '/label',
          children: [
            { path: '/label', component: Label },
            { path: '/id', component: Id }
          ]
        },
        { path: '/report', component: Report },
        // 后台部分
        // 任务列表详情
        { path: '/taskdetail', component: TaskDetail },
        { path: '/taskdetail_id', component: TaskDetailId },
        // 申请列表详情
        // 申请列表详情
        { path: '/statedetail', component: StateDetail },
        { path: '/statereject', component: StateReject },
        { path: '/statedetailExam', component: StateDetailExam },
        { path: '/statedetailReject', component: StateDetailReject },
        // 内容管理编辑页面
        { path: '/contentedit', component: ContentEdit }
      ]
    },
    {
      path: '/background',
      component: Background,
      redirect: '/applymanage',
      children: [
        { path: '/notfound', component: NotFound },
        { path: '/bg_search', component: BgSearch },
        { path: '/bg_personal', component: BgPersonal },
        { path: '/bg_group', component: BgGroup },
        { path: '/labelmanage', component: labelManage },
        { path: '/taskmanage', component: TaskManage },
        { path: '/applymanage', component: ApplyManage },
        { path: '/authoritymanage', component: AuthorityManage },
        { path: '/accessconfiguration/:id', component: AccessConfiguration },
        // 权限空白页
        { path: '/authorityblank', component: AuthorityBlank },
        { path: '/contentmanage', component: ContentManage }
      ]
    }
  ]
})

router.beforeEach((to, from, next) => {
  // console.log(to.path)
  // console.log(router.app.$options.store.state.comonTitle)
  router.app.$options.store.commit('editComonTitle', {info: to.path})
  router.app.$options.store.commit('editRouterData', {info: to.path})
  next()
})

export default router



