export default {
  data () {
    return {
      input: '',
      tableData: [{
        date: '2016-05-02',
        name: '车险部',
        id: '123456',
        user: 'json',
        isAdmin: '是'
      }, {
        date: '2016-05-04',
        name: '续保部',
        id: '123456',
        user: 'black',
        isAdmin: '是'
      }, {
        date: '2016-05-01',
        name: '安全部',
        id: '123456',
        user: 'jack',
        isAdmin: '是'
      }, {
        date: '2016-05-03',
        name: '职能部',
        id: '123456',
        user: 'rose',
        isAdmin: '是'
      }],
      currentPage4: 4
    }
  },

  methods: {
    formatter (row, column) {
      return row.address
    },
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
    },
    handleDelete (index, row) {
      console.log(index, row)
      this.$confirm('此操作将永久删除该配置组, 是否继续?', '提示', {
        cancelButtonText: '取消',
        confirmButtonText: '确定',
        type: ''
      }).then(() => {
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    skip () {
      console.log(1)
    }
  }
}



